import React from 'react';
import PropTypes from 'prop-types';

function Step({children, num, title}) {
  return (
    <div>
      <h3>
        Шаг {num}. {title}
      </h3>
      {children}
    </div>
  );
}

Step.propTypes = {
  children: PropTypes.node.isRequired,
  num: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
};

export default Step;
